.elements {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px 18px;
}

@media screen and (max-width: 720px) {
  .elements {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 470px) {
  .elements {
    grid-template-columns: 1fr;
  }
}
